.wrapper {
    height: 100vh;
    display: grid;
    place-items: center;
}

.box2 {
    padding: 0 5%;
    height: 100px;
    background-color: lightblue;
    border-radius: 40px;
    width: 320px;
}

.box2 form {
    margin-top: 10px;
}

.title {
    top: 0px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: white;
    font-weight: 1000;
    font-size: 20px;
    margin: 0;
    height: 60px;
}

.chooser {
    background-color: rgb(125, 182, 201);
    width: 100%;
    height: 30px;
    border-radius: 40px;
    margin-bottom: 20px;
}

.btn {
    background-color: rgb(125, 182, 201);
    width: 100%;
    height: 30px;
    border-radius: 40px;
    color: white;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-weight: 500;
    font-size: 20px;
}